import React from 'react';
import { STATES } from '../constants/constants';

const Dropdown = ({ value, onChange }) => {
  return (
    <select value={value} onChange={onChange}>
      {STATES.map(s => <option value={s} key={s}>{s}</option>)}
    </select>
  );
}

export default Dropdown;


