import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getMembers, clearMembers } from './actions/actions_index';
import { STATES } from './constants/constants';
import './App.css';
import Dropdown from './components/Dropdown';
import MembersList from './components/MembersList';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      STATE: 'AK',
      senate: false,
      house: false
    }
  }

  onQuerySubmit(event) {
    event.preventDefault();
    this.props.clearMembers();
    let { STATE, senate, house } = this.state;
    if (senate) {
      this.props.getMembers(STATE, 'senate');
    }
    if (house) {
      this.props.getMembers(STATE, 'house');
    }
  }

  onDropdownChange(event) {
    event.preventDefault();
    this.setState({ STATE: event.target.value });
  }

  render() {
    return (
      <div className="App">

        <div className="query">
          <Dropdown
            value={this.state.value}
            onChange={e => this.onDropdownChange(e)}
          />

          <div>
            <span>search in:</span>
            <label>
              <input
                name="senate"
                type="checkbox"
                checked={this.state.senate}
                onChange={e => this.setState({ senate: !this.state.senate })}
              />
              Senate
          </label>
            <label>
              <input
                name="house"
                type="checkbox"
                checked={this.state.house}
                onChange={e => this.setState({ house: !this.state.house })}
              />
              House of Representatives
        </label>
          </div>
        </div>

        {
          this.state.senate || this.state.house
            ? <button className='submitButton' onClick={e => this.onQuerySubmit(e)}>find my representatives</button>
            : ''
        }

        <MembersList />

      </div>
    );
  }
}

export default connect(null, { getMembers, clearMembers })(App);



